:root{
    --clr-purple-dark: rgb(95, 0, 186);
    --clr-purple-light: rgb(170, 125, 206);
    --clr-purple-bg: rgb(235, 199, 255);
    --clr-orange-accent-dark: #F6A81C;
    --clr-orange-accent-light: #F7D22B;
  }

/*
||  GENERAL STYLES
*/
*,
*::before,
*::after {
    box-sizing: border-box;
    cursor: url('../media/img/cursor.png'), default;
}
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    min-height: auto;
    background: var(--clr-purple-bg);
    overflow-x: hidden;
}

.app {
    text-align: center;
    letter-spacing: .25px;
    min-height: 100%;
}

/*
||  NAVIGATION BAR
*/

// Top
.navTop {
    display: flex;
    height: auto;
    background: rgb(95, 0, 186);
    width: 100%;
    justify-content: space-around;
    box-shadow: 0 3px 15px rgba(black, .3);
    z-index: 10;
}

.navLinks {
    display: flex;
    justify-content: space-around;
    list-style: none;
    padding-left: 0%;
    margin-top: 0;
    z-index: auto;
}

.navLinkItem {
    display: flex;
    font-size: 1.5rem;
    font-family: "Fengardoneue-Regular";
    color: white;
    text-decoration: none;
    position: relative;
    transition: .2s;
    cursor: url('../media/img/HandFinger.png'), pointer;
}

.navLinkItem::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 3px;
    background-color: currentColor;
    z-index: 4;

    transform: scaleX(0);
    transform-origin: right;
    transition: transform .25s ease-in;
    color: rgb(170, 125, 206);
}
.navLinkItem:hover:after {
    transform: scaleX(1);
    transform-origin: left;
}

.navLinkItemHome {
    font-family: 'Heavitas';
    text-decoration: none;
    font-size: 4vw;
    color: white;
    padding: 0;
    transition: .3s ease-in;
    cursor: url('../media/img/HandFinger.png'), pointer;
}
.navLinkItemHome:hover {
    color: rgb(170, 125, 206);
}
@media screen and (max-width: 800px) {
    .navLinkItemHome {
        font-size: 2rem;
        letter-spacing: .2rem;
        transition: ease all .5s;
    }
}

#logoWhite {
    width: 1ch;
    margin-right: 3px;
    transform: translateY(5%);
}